import React from 'react'
import { OverlayProps } from '../../interface'
import './index.css'

const year = new Date().getFullYear()
const build = process.env.build

export function WelcomeNotice({ version, active }: OverlayProps) {
  if (!active) return null

  return (
    <div className="notice-welcome">
      <p> Matcha </p>
      <p></p>
      <ol>
        <li>
          <span className="tag">ngld </span>
        </li>
        <li>
          <span className="tag"> Matcha</span>
        </li>
      </ol>
      <p>
        {(window as any).TARGET_MATCHA_VERSION}
        {version || '<>'}
      </p>
      <p className="copyright">
        &copy; {year} ReiwaTech / Build: {build}
      </p>
    </div>
  )
}
